<template>
  <div :class="필터 + ` filter-item`" :style="{backgroundImage: `url(${이미지업로드})`}">
    <slot></slot>
    <button @click="fire">버튼</button>
  </div>
</template>

<script>
export default {
  name: 'FilterBox-FilterBox',
  components: {

  },
  props: {
    이미지업로드 : String,
    필터 :  String,
  },
  methods: {
    fire() {
      this.emitter.emit('필터명', this.필터)
    }
  }
}
</script>

