import { createStore } from 'vuex'
import axios from 'axios'

const store = createStore({
    state() {
        return {
            name : 'kim',
            age : [20, 30, 40],
            likes : 30,
            liked : false,
            more : {},
        }
    },
    mutations : {
        이름변경(state) {
            state.name = 'park'
        },
        나이증가(state, data) {
            state.age += data;
        },
        좋아요(state) {
            if (state.liked === false) {
                state.likes++;
                state.liked = true;
                console.log(state.liked)
            }
            else if (state.liked === true) {
                state.likes--;
                state.liked = false;
                console.log(state.liked)
            }
        },
        setMore(state, data){
            state.more = data
        }
    },
    actions : {
        getData(context) {
          axios.get('https://codingapple1.github.io/vue/more0.json')
              .then((a)=>{
                  console.log(a.data);
                  context.commit('setMore', a.data);
          })
        },

        서버확인() {
            axios.get('http://localhost:8089/list')
                .then((a)=>{
                console.log(a)
            })
        }
    }
})

export default store;