<template>
  <div style="padding : 10px">
    <h4>팔로워</h4>
    <input placeholder="" @input="검색($event.target.value)"/>
    <div class="post-header" v-for="(작명, i) in follower" :key="작명">
      <div class="profile" :style="{backgroundImage: `url(${follower[i].image})`}"></div>
      <span class="profile-name">{{ follower[i].name }}</span>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, toRefs } from 'vue';
import axios from "axios";
export default {
  name: 'MyPage-MyPage',
  props : {
    one : Number,
  },

  setup(props) {
    let follower = ref([]);
    let originalFollower = ref([]);


    let { one } =  toRefs(props);
    console.log(one.value)

    function 검색(a) {
      follower.value = originalFollower.value.filter(follower => follower.name.includes(a));
    }

    onMounted(() => {
      axios.get('/follower.json').then((a)=>{
        originalFollower.value = a.data
        follower.value = a.data
      })
    })


    return { follower, 검색 }
  },
  data() {
    return {

    }
  },
  components: {

  },
}
</script>

