<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step===1" @click="step++">Next</li>
      <li v-if="step===2" @click="publish">발행</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>

<!--  <h4>안녕 {{ $store.state.name }}</h4>-->
<!--  <h4>나이 {{ $store.state.age }}</h4>-->
<!--  <button @click="이름변경()">이름좀 바꿔볼꽈~?</button>-->
<!--  <button @click="$store.commit('나이증가', 10)">나이좀 증가시켜볼꽈~?</button>-->
<!--  <p>{{ $store.state.more }}</p>-->
<!--  <button @click="$store.dispatch('getData')">더보기버튼</button>-->
<!--  <p>{{ age[0] }}</p>-->


  <Container :인스타데이터="인스타데이터" :step="step" :이미지업로드="이미지업로드"
             :텍스트="텍스트" :씌울필터="씌울필터" @setOne="step = 0" @write ="텍스트 = $event"
             @setTwo="step = 1" @setThree="step = 2"/>
  <button @click="more">더보기</button>


  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" accept="image/*" multiple type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus" >+</label>
    </ul>
  </div>




</template>

<script>
import Container from "./components/Container.vue";
import Data from "./components/InstaData"
import axios from "axios";
import {mapActions, mapMutations, mapState} from "vuex";

export default {
  name: 'App',
  components: {
    Container : Container,
  },
  data() {
    return {
      인스타데이터 : Data,
      버튼횟수 : 0,
      step : 0,
      이미지업로드 : ' ',
      텍스트 : ' ',
      씌울필터 : ' ',
    }
  },
  mounted() {
    this.emitter.on('필터명', (필터)=>{
      this.씌울필터 = 필터;
    })
  },
  methods : {
    ...mapMutations(['이름변경', '좋아요']),
    ...mapActions(['서버확인']),

    more() {
      axios.get(`https://codingapple1.github.io/vue/more${ this.버튼횟수 }.json`)
          .then((결과)=>{
            this.인스타데이터.push(결과.data)
          })
      this.버튼횟수++
    },
    upload(e) {
      let 파일 = e.target.files;
      this.이미지업로드 = URL.createObjectURL(파일[0])
      this.step++;
    },
    publish() {
      var 내게시물 = {
            name: "Kim Hyun",
            userImage: "https://picsum.photos/100?random=3",
            postImage: this.이미지업로드,
            likes: 0,
            date: "May 15",
            liked: false,
            content: this.텍스트,
            filter: this.씌울필터,
          };
      this.인스타데이터.unshift(내게시물)
      this.step = 0;
    },
    now() {
      axios.get('http://localhost:8089/list')
          .then((a)=>{
            console.log(a)
          })
    }
  },
  computed : {
    ...mapState(['name', 'age', 'likes']),
    ...mapState({ 작명 : 'name', })
  }
}
</script>

